import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);
const routes = [
  {
    path: '/',
    component: Home,
    name: 'home',
  },
  {
    path: '*',
    component: Home,
  },
];

const router = new VueRouter({
  // mode: 'hash',
  mode: 'history',
  base: './',
  routes,
});

export default router;
